import { create } from 'zustand';
import { persist } from 'zustand/middleware';
//  Axios 
import { api, setAuthToken } from '../config/api';
import toast from 'react-hot-toast';

interface User {
  id: number;
  username: string;
  email?: string;
  phone?: string;
  avatar_url?: string;
}

interface AuthState {
  isAuthenticated: boolean;
  user: User | null;
  token: string | null;
  hydrated: boolean; // 
  setHasHydrated: (value: boolean) => void;
  setUser: (patch: Partial<User> | User) => void;
  login: (username: string, password: string) => Promise<boolean>;
  logout: () => void;
  register: (username: string, password: string, email?: string, phone?: string) => Promise<boolean>;
}

export const useAuthStore = create<AuthState>()(
  persist(
    (set, get) => ({
      isAuthenticated: false,
      user: null,
      token: null,
      hydrated: false,
      setUser: (patch: Partial<User> | User) => set((state: AuthState) => ({
        user: state.user ? { ...state.user, ...patch } as User : (patch as User),
      })),
  setHasHydrated: (value: boolean) => set({ hydrated: value }),

      login: async (username: string, password: string) => {
        try {
          const response = await api.post('/api/auth/login', {
            username,
            password,
          });

          const { token, user } = response.data;
          
          //  Authorization header
          setAuthToken(token);
          
          set({
            isAuthenticated: true,
            user,
            token,
          });

          toast.success('');
          return true;
        } catch (error: any) {
          const message = error.response?.data?.message || '';
          toast.error(message);
          return false;
        }
      },

      logout: () => {
        //  Authorization header
  setAuthToken(undefined);
        
        set({
          isAuthenticated: false,
          user: null,
          token: null,
        });

        toast.success('');
      },

      register: async (username: string, password: string, email?: string, phone?: string) => {
        try {
          // API
          const healthResponse = await api.get('/health');
          
          const response = await api.post('/api/auth/register', {
            username,
            password,
            email,
            phone,
          });
          
          const { token, user } = response.data;
          
          if (!token || !user) {
            toast.error('');
            return false;
          }
          
          //  Authorization header
          setAuthToken(token);
          
          set({
            isAuthenticated: true,
            user,
            token,
          });

          toast.success('');
          return true;
        } catch (error: any) {
          console.error(' :', error);
          console.error(' :', error.constructor.name);
          console.error(' :', error.code);
          
          if (error.response) {
            console.error(' :', error.response.status, error.response.data);
          } else if (error.request) {
            console.error(' :', error.request);
          }
          
          if (error.code === 'ERR_NETWORK') {
            toast.error('');
          } else if (error.response?.status === 409) {
            toast.error('');
          } else if (error.response?.status === 400) {
            toast.error('');
          } else {
            const message = error.response?.data?.error || error.response?.data?.message || '';
            toast.error(message);
          }
          return false;
        }
      },
    }),
    {
      name: 'auth-storage',
      partialize: (state) => ({
        isAuthenticated: state.isAuthenticated,
        user: state.user,
        token: state.token,
      }),
      onRehydrateStorage: () => (state: any) => {
        //  Authorization 
        if (state?.token) {
          setAuthToken(state.token || undefined);
        } else {
          setAuthToken(undefined);
        }
        state?.setHasHydrated?.(true);
      },
    }
  )
);