//  px -> rem  spacing/typography 
//  root 16px html clamp 

export const BASE_FONT_SIZE = 16;
export const pxToRem = (px: number) => `${px / BASE_FONT_SIZE}rem`;

export const spacing = {
  //  / 
  hair: pxToRem(1),
  micro: pxToRem(2),
  xxs: pxToRem(4),
  xs: pxToRem(6),
  sm: pxToRem(8),
  smd: pxToRem(12),
  md: pxToRem(16),
  mlg: pxToRem(20),
  lg: pxToRem(24),
  xlg: pxToRem(28),
  xl: pxToRem(32),
  xxl: pxToRem(40),
};

export const typography = {
  display: pxToRem(32),
  h1: pxToRem(24),
  h2: pxToRem(20),
  h3: pxToRem(18),
  body: pxToRem(16),
  small: pxToRem(14),
  caption: pxToRem(12),
  micro: pxToRem(11),
};

