/**
 *  Store
 *  Zustand + persist 
 * 
 * Purpose:  localStorage
 * Input: 
 * Output: 
 * Errors: localStorage 
 */

import { create } from 'zustand';
import { persist, createJSONStorage } from 'zustand/middleware';
import {
  NotificationSettings,
  DEFAULT_NOTIFICATION_SETTINGS,
  VibrationPatterns,
} from '../types/notifications';

/**
 *  Store 
 */
interface SettingsState extends NotificationSettings {
  // 
  autoReply: boolean;
  autoReplyMessage: string;
  
  // 
  darkMode: boolean;
  language: string;
  fontSize: 'small' | 'medium' | 'large';
  
  // 
  messagePreview: boolean; // 
  
  // Actions
  updateNotificationSettings: (settings: Partial<NotificationSettings>) => void;
  toggleNotifications: () => void;
  toggleSound: () => void;
  toggleVibration: () => void;
  setSoundVolume: (volume: number) => void;
  setVibrationPattern: (pattern: number[]) => void;
  
  toggleAutoReply: () => void;
  setAutoReplyMessage: (message: string) => void;
  
  toggleDarkMode: () => void;
  setLanguage: (language: string) => void;
  setFontSize: (size: 'small' | 'medium' | 'large') => void;
  
  toggleMessagePreview: () => void;
  
  // 
  resetToDefaults: () => void;
}

/**
 * 
 */
const DEFAULT_SETTINGS: Omit<
  SettingsState,
  | 'updateNotificationSettings'
  | 'toggleNotifications'
  | 'toggleSound'
  | 'toggleVibration'
  | 'setSoundVolume'
  | 'setVibrationPattern'
  | 'toggleAutoReply'
  | 'setAutoReplyMessage'
  | 'toggleDarkMode'
  | 'setLanguage'
  | 'setFontSize'
  | 'toggleMessagePreview'
  | 'resetToDefaults'
> = {
  ...DEFAULT_NOTIFICATION_SETTINGS,
  autoReply: false,
  autoReplyMessage: '',
  darkMode: false,
  language: 'zh-CN',
  fontSize: 'medium',
  messagePreview: true,
};

/**
 *  Store
 */
export const useSettingsStore = create<SettingsState>()(
  persist(
    (set, get) => ({
      // 
      ...DEFAULT_SETTINGS,

      //  Actions
      updateNotificationSettings: (settings) => {
        set((state) => ({
          ...state,
          ...settings,
        }));
      },

      toggleNotifications: () => {
        set((state) => ({
          notifications: !state.notifications,
        }));
      },

      toggleSound: () => {
        set((state) => ({
          soundEnabled: !state.soundEnabled,
        }));
      },

      toggleVibration: () => {
        set((state) => ({
          vibrationEnabled: !state.vibrationEnabled,
        }));
      },

      setSoundVolume: (volume) => {
        set({
          soundVolume: Math.max(0, Math.min(1, volume)),
        });
      },

      setVibrationPattern: (pattern) => {
        set({ vibrationPattern: pattern });
      },

      //  Actions
      toggleAutoReply: () => {
        set((state) => ({
          autoReply: !state.autoReply,
        }));
      },

      setAutoReplyMessage: (message) => {
        set({ autoReplyMessage: message });
      },

      //  Actions
      toggleDarkMode: () => {
        set((state) => ({
          darkMode: !state.darkMode,
        }));
      },

      setLanguage: (language) => {
        set({ language });
      },

      setFontSize: (size) => {
        set({ fontSize: size });
      },

      //  Actions
      toggleMessagePreview: () => {
        set((state) => ({
          messagePreview: !state.messagePreview,
        }));
      },

      // 
      resetToDefaults: () => {
        set(DEFAULT_SETTINGS);
      },
    }),
    {
      name: 'customer-service-settings', // localStorage key
      storage: createJSONStorage(() => localStorage),
      
      // 
      partialize: (state) => ({
        // 
        notifications: state.notifications,
        soundEnabled: state.soundEnabled,
        vibrationEnabled: state.vibrationEnabled,
        soundVolume: state.soundVolume,
        vibrationPattern: state.vibrationPattern,
        
        // 
        autoReply: state.autoReply,
        autoReplyMessage: state.autoReplyMessage,
        
        // 
        darkMode: state.darkMode,
        language: state.language,
        fontSize: state.fontSize,
        
        // 
        messagePreview: state.messagePreview,
      }),

      // 
      version: 1,

      // 
      migrate: (persistedState: any, version: number) => {
        // 
        if (version === 0) {
          // 
          return {
            ...DEFAULT_SETTINGS,
            ...persistedState,
          };
        }
        return persistedState as SettingsState;
      },
    }
  )
);

/**
 * 
 */
export const getNotificationSettings = (): NotificationSettings => {
  const state = useSettingsStore.getState();
  return {
    notifications: state.notifications,
    soundEnabled: state.soundEnabled,
    vibrationEnabled: state.vibrationEnabled,
    soundVolume: state.soundVolume,
    vibrationPattern: state.vibrationPattern,
  };
};

/**
 * 
 */
export const shouldNotify = () => {
  const state = useSettingsStore.getState();
  return {
    shouldPlaySound: state.notifications && state.soundEnabled,
    shouldVibrate: state.notifications && state.vibrationEnabled,
    shouldShowNotification: state.notifications,
    soundVolume: state.soundVolume,
    vibrationPattern: state.vibrationPattern,
  };
};

/**
 * 
 */
export const VibrationPatternPresets = VibrationPatterns;
