/**
 * 
 * 
 */

/**
 * 
 */
export interface NotificationSettings {
  /**  */
  notifications: boolean;
  /**  */
  soundEnabled: boolean;
  /**  */
  vibrationEnabled: boolean;
  /**  (0-1) */
  soundVolume: number;
  /**  */
  vibrationPattern: number[];
}

/**
 * 
 */
export type NotificationPermissionState = 'default' | 'granted' | 'denied';

/**
 * 
 */
export interface NotificationOptions {
  /**  */
  title: string;
  /**  */
  body?: string;
  /**  */
  icon?: string;
  /**  */
  tag?: string;
  /**  */
  requireInteraction?: boolean;
  /**  */
  silent?: boolean;
  /**  */
  data?: any;
  /** / */
  onClick?: (payload: { notification: Notification; data?: any }) => void;
}

/**
 * 
 */
export interface MessageNotificationOptions {
  /**  */
  playSound?: boolean;
  /**  */
  vibrate?: boolean;
  /**  */
  showNotification?: boolean;
  /**  */
  senderName?: string;
  /**  */
  messageContent?: string;
  /** ID */
  shopId?: number;
  /** ID */
  sessionId?: number;
  /**  */
  onClick?: (payload: { shopId?: number; sessionId?: number }) => void;
}

/**
 * 
 */
export const VibrationPatterns = {
  /**  (200ms) */
  short: [200],
  /**  (400ms) */
  medium: [400],
  /**  (600ms) */
  long: [600],
  /**  (200ms, 100ms, 200ms) */
  double: [200, 100, 200],
  /**  */
  triple: [200, 100, 200, 100, 200],
  /**  */
  pattern: [100, 50, 100, 50, 100, 50, 400],
} as const;

/**
 * 
 */
export const DEFAULT_NOTIFICATION_SETTINGS: NotificationSettings = {
  notifications: true,
  soundEnabled: true,
  vibrationEnabled: true,
  soundVolume: 1.0,
  vibrationPattern: [200],
};
