import React from 'react';
import styled from 'styled-components';
import { FiCalendar } from 'react-icons/fi';
import { useNavigate } from 'react-router-dom';
import { theme } from '../../styles/globalStyles';
import { useDashboardStats } from '../../hooks/useDashboardStats';
import { PeriodStats, TrendAnalysis, DataSummary, RefreshStatsButton } from '../../components/Statistics';
import { Section, SectionTitle } from '../../components/UI';

const Container = styled.div`
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  flex-direction: column;
`;

const ScrollableContent = styled.div`
  flex: 1;
  overflow-y: auto;
  padding: ${theme.spacing.md} ${theme.spacing.md} ${theme.spacing.xxl};
  
  /*  */
  &::-webkit-scrollbar {
    width: 6px;
  }
  
  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 3px;
  }
  
  &::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.3);
    border-radius: 3px;
    
    &:hover {
      background: rgba(255, 255, 255, 0.5);
    }
  }
`;


const StatisticsPage: React.FC = () => {
  const { stats, isLoading, isRefreshing, refreshStats, lastUpdated } = useDashboardStats({
    autoRefreshInterval: 60000, // 1
    enableAutoRefresh: true
  });

  //  (PeriodStats)
  const periodData = [
    {
      period: 'today',
      label: '',
      metrics: [
        { label: '', value: stats.todayMessages },
        { label: '', value: stats.todayCustomers }
      ]
    },
    {
      period: 'week',
      label: '',
      metrics: [
        { label: '', value: stats.weekMessages },
        { label: '', value: Math.round(stats.weekMessages / 7) }
      ]
    },
    {
      period: 'month',
      label: '',
      metrics: [
        { label: '', value: stats.monthMessages },
        { label: '', value: Math.round(stats.monthMessages / 4) }
      ]
    }
  ];

  return (
    <Container>
  {/*  */}

      <ScrollableContent>
        {/*  */}
        <DataSummary
          todayMessages={stats.todayMessages}
          weekMessages={stats.weekMessages}
          monthMessages={stats.monthMessages}
          activeCustomers={stats.activeCustomers}
          pendingChats={stats.pendingChats}
          totalShops={stats.totalShops}
        />

        {/**  DataInsights */}

        {/*  */}
        <TrendAnalysis
          todayMessages={stats.todayMessages}
          weekMessages={stats.weekMessages}
          monthMessages={stats.monthMessages}
          todayCustomers={stats.todayCustomers}
          activeCustomers={stats.activeCustomers}
        />

        {/*  */}
        <Section>
          <SectionTitle>
            <FiCalendar />
            
          </SectionTitle>
          <PeriodStats 
            periods={periodData}
            isLoading={isLoading}
          />
        </Section>

        {/*  */}
        <Section>
          <RefreshStatsButton
            onRefresh={refreshStats}
            isRefreshing={isRefreshing}
          />
        </Section>
      </ScrollableContent>
    </Container>
  );
};

export default StatisticsPage;