import { create } from 'zustand';
import { staffSocket } from '../config/ws';
import { useAuthStore } from './authStore';
import { useConversationsStore } from './conversationsStore';
import { useNotificationsStore } from './notificationsStore';
import { normalizeWSMessage, makeDedupKey } from '../utils/wsEvents';
import { notificationService } from '../services/notificationService';
import { useUIStore } from './uiStore';
import { shouldNotify, useSettingsStore } from './settingsStore';

type WSStatus = 'disconnected' | 'connecting' | 'connected' | 'error';

// 
type MessageListener = (data: any) => void;

interface WSState {
  status: WSStatus;
  socket?: WebSocket;
  activeShopId?: number;
  messageListeners: MessageListener[];
  // key -> lastSeenEpochMs
  dedupCache: Record<string, number>;
  reconnectAttempts?: number;
  reconnectTimer?: any;
  heartbeatTimer?: any;
  connect: (shopId: number) => void;
  disconnect: () => void;
  addMessageListener: (listener: MessageListener) => void;
  removeMessageListener: (listener: MessageListener) => void;
}

export const useWSStore = create<WSState>((set, get) => ({
  status: 'disconnected',
  socket: undefined,
  activeShopId: undefined,
  messageListeners: [],
  dedupCache: {},
  reconnectAttempts: 0,
  reconnectTimer: undefined,
  heartbeatTimer: undefined,

  connect: (shopId: number) => {
    const { user } = useAuthStore.getState();
    if (!user) return;

    // 
    const existing = get().socket;
    if (existing && get().status === 'connected' && get().activeShopId === shopId) {
      return;
    }

    if (existing) {
      try {
        // 
        existing.onopen = null as any;
        existing.onmessage = null as any;
        existing.onerror = null as any;
        existing.onclose = null as any;
      } catch {}
      try { existing.close(); } catch {}
    }

    // 
    const prevTimer = get().reconnectTimer;
    if (prevTimer) {
      clearTimeout(prevTimer);
      set({ reconnectTimer: undefined });
    }

    set({ status: 'connecting', activeShopId: shopId });
    const ws = staffSocket(String(user.id));

    ws.onopen = () => {
      //  staff auth shopId
      const authMsg = {
        messageType: 'auth',
        metadata: { shopId },
      };
      ws.send(JSON.stringify(authMsg));
      //  25s  ping
      try {
        const prevHb = get().heartbeatTimer;
        if (prevHb) clearInterval(prevHb);
      } catch {}
      const hb = setInterval(() => {
        try {
          if (get().socket && get().status === 'connected') {
            get().socket!.send(JSON.stringify({ messageType: 'ping', ts: Date.now() }));
          }
        } catch (e) {
          console.warn('WS ', e);
          try { ws.close(); } catch {}
        }
      }, 25000);

      set({ status: 'connected', reconnectAttempts: 0, heartbeatTimer: hb });
      console.log(' WebSocket ');
    };

    ws.onmessage = (ev: MessageEvent) => {
      try {
        const data = JSON.parse(ev.data);
        const n = normalizeWSMessage(data);
        console.log(' wsStore():', n);
        
        //  new_message/
        if (n.type === 'new_message') {
          const now = Date.now();
          const key = makeDedupKey(n);
          const cache = get().dedupCache;
          // >10s
          for (const k in cache) {
            if (now - cache[k] > 10000) delete cache[k];
          }
          if (cache[key] && now - cache[key] < 3000) {
            // 3 
            return;
          }
          cache[key] = now;
          set({ dedupCache: { ...cache } });
        }

        // 
        const currentState = get();
        console.log(' :', currentState.messageListeners.length);
        
        currentState.messageListeners.forEach((listener, index) => {
          try {
            console.log(`  ${index}:`, n.raw);
            listener(n.raw);
          } catch (error) {
            console.error(`  ${index} :`, error);
          }
        });
        
        //  store
        if (n.type === 'new_message') {
          // 
          const senderType = n.senderType as string | undefined;
          const shopId = currentState.activeShopId ?? n.shopId;
          const sessionId = n.sessionId;
          console.log(' :', { shopId, senderType, sessionId });
          if (shopId && senderType === 'customer') {
            useConversationsStore.getState().incrementUnread(shopId, 1);
            // 
            if (sessionId) {
              useNotificationsStore.getState().incrementSessionUnread(sessionId, 1, shopId);
            } else {
              // ID +1
              useNotificationsStore.getState().incrementShopUnread(shopId, 1);
            }

            //  
            // 
            const activeSessionId = useUIStore.getState().activeSessionId;
            const isPageVisible = typeof document !== 'undefined' && document.visibilityState === 'visible';
            const shouldSuppress = isPageVisible && !!sessionId && activeSessionId === sessionId;

            if (!shouldSuppress) {
              const notifySettings = shouldNotify();
              const preview = useSettingsStore.getState().messagePreview;
              notificationService.notifyNewMessage({
                playSound: notifySettings.shouldPlaySound,
                vibrate: notifySettings.shouldVibrate,
                showNotification: notifySettings.shouldShowNotification,
                senderName: '', // 
                messageContent: preview ? (n.content as string | undefined) : '',
                shopId,
                sessionId,
                onClick: ({ shopId: sId, sessionId: sessId }) => {
                  try { window.focus(); } catch {}
                  try {
                    const ev = new CustomEvent('open-session', { detail: { shopId: sId, sessionId: sessId } });
                    window.dispatchEvent(ev);
                  } catch (e) {
                    console.debug('', e);
                  }
                },
              }).catch((error) => {
                // 
                console.debug(':', error);
              });
            }
          }
        } else if (n.type === 'typing') {
          // typing  UI 
        }
      } catch (e) {
        console.error(' wsStore:', e);
      }
    };

    ws.onerror = () => {
      set({ status: 'error' });
      console.error(' WebSocket ');
    };

    ws.onclose = () => {
      const { activeShopId, reconnectAttempts } = get();
      //  socket 
      const prevHb = get().heartbeatTimer;
      if (prevHb) {
        try { clearInterval(prevHb); } catch {}
      }
      set({ status: 'disconnected', socket: undefined, heartbeatTimer: undefined });
      console.log(' WebSocket ');

      //  shop  30s
      if (activeShopId) {
        const attempts = (reconnectAttempts || 0) + 1;
        const delay = Math.min(30000, 1000 * Math.pow(2, attempts - 1));
        const timer = setTimeout(() => {
          try {
            get().connect(activeShopId);
          } catch (e) {
            console.warn(' WebSocket :', e);
          }
        }, delay);
        set({ reconnectAttempts: attempts, reconnectTimer: timer });
      }
    };

    set({ socket: ws });
  },

  disconnect: () => {
    const sock = get().socket;
    if (sock) {
      try { sock.close(); } catch {}
    }
    const prevHb = get().heartbeatTimer;
    if (prevHb) {
      try { clearInterval(prevHb); } catch {}
    }
    set({ socket: undefined, status: 'disconnected', activeShopId: undefined, heartbeatTimer: undefined });
  },

  addMessageListener: (listener: MessageListener) => {
    const { messageListeners } = get();
    // 
    if (messageListeners.includes(listener)) {
      console.log(' ');
      return;
    }
    const newListeners = [...messageListeners, listener];
    set({ messageListeners: newListeners });
    console.log(' :', newListeners.length);
  },

  removeMessageListener: (listener: MessageListener) => {
    const { messageListeners } = get();
    const newListeners = messageListeners.filter(l => l !== listener);
    set({ messageListeners: newListeners });
    console.log(' :', newListeners.length);
  },
}));

// /
try {
  if (typeof window !== 'undefined') {
    const reconnectIfNeeded = () => {
      const st = useWSStore.getState();
      if (st.activeShopId && st.status !== 'connected') {
        try { st.connect(st.activeShopId); } catch {}
      }
    };
    window.addEventListener('online', reconnectIfNeeded);
    document.addEventListener('visibilitychange', () => {
      if (document.visibilityState === 'visible') reconnectIfNeeded();
    });
    window.addEventListener('beforeunload', () => {
      try { useWSStore.getState().disconnect(); } catch {}
    });
  }
} catch {}
